<script>

    import {mapState} from 'vuex'
    import {setLang} from '../../lang/lang'
    import {eventSourceConn, handleMsg} from '../restfulapi/eventSourceApi'
    import {getOnlineDeviceCountApi} from '../restfulapi/deviceApi'
    import handleResponse from '../restfulapi/handleResponse'
    import profileDialog from "./profile"
    import {getSession, setLocal, getLocal, removeLocal, checkLocal, setSession, checkSession} from "../../assets/js/storage"
    import {getUserByNameApi, resetPasswd} from "../restfulapi/userInfo"
    import {applicationTitle, releaseDate, power, version} from "../../assets/js/constant"
    import qrCode from '@chenfengyuan/vue-qrcode';
    import {getQRcodeDataApi, getRepoServerApi, getServerConfigApi, getServerIdApi} from "../restfulapi/serverConfigApi"
    import agentDialog from "./agentDialog"
    import edgeOnDialog from "./edgeOnDialog"
    import menuOnDialog from "./menuOnDialog"
	const copy = require("clipboard-copy");

    export default{
        name: 'mainHeader',
        data(){
            let validatePwd = (rule,value,callback)=>{
                let reg=new RegExp(/^(?=.*[0-9])(?=.*[a-z])(?=.*[A-Z])(?=.*[!#$%&'()*+,-./:;<=>?@\u005B\u005C\u005D^_`{|}~])(?=\S+$)(?!.*[\u0022\u005C]).{8,20}/);
                if(!reg.test(value)){
                    return callback(new Error (this.$t('global.verifySsoPwdError')))
                }else{callback()};
            };
            return {
                img:"",
                appTitle: applicationTitle,
                qrCodeValue: "",
                username:'',
                logintime:'',
                devicecount:'',
                msgData: [],
                allowleft: "fa fa-long-arrow-left",
                allowright: "fa fa-bars",
                activeItem: 'device-devicelist',
                dialogResetPasswordVisible: false,
                formLabelWidth: "200px",
                ResetPasswordForm: {
                    oldPwd: "",
                    newPwd: "",
                    confirmNewPwd: ""
                },
                rules: {
                    oldPwd: {validator: validatePwd, trigger: 'blur'},
                    newPwd: {validator: validatePwd, trigger: 'blur'},
                    confirmNewPwd: {validator: validatePwd, trigger: 'blur'},
                },

                qrIsShow: false,
                msgCount: 0,
                msgLimit: 6,
                isShake: false,

                role: "",
                releaseDate: releaseDate,
                version: version,
                configValue: "",
                serverId: "",
                serverIdIsShow: false

            }
        },
        components: {
            profileDialog,
            qrCode,
            agentDialog,
            edgeOnDialog,
            menuOnDialog
        },

        props:{
            isCollapse: {
                type: Boolean,
                default: false
            }
        },

        methods:{
            setting(value){
                switch(value){
                    case "license": 
                        this.$router.replace("/main/license/list");
                        break;
                    case "agentDownload": 
                        this.$refs.agentDialog.agentDialogVisible = true;
                        break;
                    case "edgeOn": 
                        this.$refs.edgeOnDialog.edgeOnDialogVisible = true;
                         break;
                    case "menu": 
                        this.$refs.menuOnDialog.menuOnDialogVisible = true;
                         break;
                    case "config":
                         this.$router.replace("/main/configuration/server");
                        break;

                }
            },
            
            getuserinfo(){
                this.username = getSession("username");
                this.logintime = getSession("logintime");
                getOnlineDeviceCountApi().then((data) => {
                    handleResponse(data, (res) => { 
                        this.devicecount = res.count;
                    })
                })
            },

            loginout(){
                this.$router.replace('/');
            },

            markAll(){
                this.msgData = [];
                this.msgCount=0
                removeLocal("msgData");   
                removeLocal("msgCount");
            },

            viewAll(){
                this.markAll();
                this.$router.push('/main/eventMsg/list')
            },

            showMsg(){
                if(checkLocal("msgData")&&checkLocal("msgCount")){
                    let msgLocalData = getLocal('msgData');
                    this.msgCount = getLocal("msgCount");
                    msgLocalData = JSON.parse(msgLocalData);
                    this.msgData = msgLocalData;
                    this.msgData.sort((a,b) => {
                        new Date(a.registrationDate).getTime()> new Date(b.registrationDate).getTime()
                    })
                }               
            },

            switchLang(lang){
                if(lang){
                    // 6
                    cookie.setCookie("link_lang", lang, 60*24*30*6);
                    setLang(lang);
                    let count = this.switchLangCount;
                    if(count>0){
                        count--; 
                    }else{
                        count++;
                    }
                    _g.switchLangCount(count);
                }
            },

            doEventSource(){
                eventSourceConn().then((isConnect) => {
                    if(isConnect){
                        handleMsg("REGISTRATION", (type, data) => {
                            if(data){
                                this.isShake = true;
                                window.setTimeout(() => {
                                    this.isShake = false;
                                }, 2000)
                                let count = this.onlineDeviceCount; 
                                count++; 
                                _g.onlineDeviceCount(count);
                                let msgdata = {};
                                
                                let msgObj = JSON.parse(data);
                                msgdata.endpoint = msgObj.endpoint;
                                msgdata.registrationDate = msgObj.registrationDate;
                                msgdata.timestamp = msgObj.timestamp;
                                msgdata.devName = msgObj.devName;
                                msgdata.type = type;
                                if(this.msgData.length> this.msgLimit|| this.msgData.length== this.msgLimit){
                                    this.msgData.pop();
                                }
                                this.msgData.unshift(msgdata);
                                this.msgCount++;
                                setLocal("msgData", this.msgData);
                                setLocal("msgCount", this.msgCount);
                                
                            }
                            
                        }, false);
                        handleMsg("DEREGISTRATION", (type, data) => {
                            if(data){
                                this.isShake = true;
                                window.setTimeout(() => {
                                    this.isShake = false;
                                }, 2000)
                                //onlineDeviceCount --
                                let count = this.onlineDeviceCount; 
                                count--;
                                let msgdata = {};
                                //update onlineDeviceCount
                                _g.onlineDeviceCount(count);
                                let msgObj = JSON.parse(data);
                                msgdata.endpoint = msgObj.endpoint;
                                msgdata.registrationDate = msgObj.registrationDate;
                                msgdata.timestamp = msgObj.timestamp;
                                msgdata.devName = msgObj.devName;
                                msgdata.type = type;
                                if(this.msgData.length> this.msgLimit|| this.msgData.length== this.msgLimit){
                                    this.msgData.pop();
                                }
                                this.msgData.unshift(msgdata);
                                this.msgCount++;
                                setLocal("msgData", this.msgData);
                                setLocal("msgCount", this.msgCount);
                            }
                        
                        }, false);  
                        handleMsg("NOTIFICATION", (type, data) => {
                            if(data){
                                let msgdata = {};
                                let msgObj = JSON.parse(data);
                                if(msgObj.id == 'overview'){
                                    let count = this.deviceStatusCount;
                                    if(count>0){
                                        count--; 
                                    }else{
                                        count++;
                                    }
                                    _g.deviceStatusCount(count);
                                    return;
                                }
                                this.isShake = true;
                                window.setTimeout(() => {
                                    this.isShake = false;
                                }, 2000)

                                msgdata.endpoint = msgObj.ep;
                                msgdata.appname = msgObj.appname;
                                msgdata.type = type;
                                var date = new Date();
                                msgdata.timestamp = date.getTime();
                                msgdata.id = msgObj.id;
                                msgdata.content = msgObj.content;
                                msgdata.devName = msgObj.devname;
                                msgdata.level = msgObj.level;
                                if(msgdata.appname == 'hardware' || msgdata.appname == 'software' || msgdata.appname == 'app'){
                                    let count = this.deviceMsgCount;
                                     if(count>0){
                                        count--; 
                                    }else{
                                        count++;
                                    }
                                    _g.deviceMsgCount(count);
                                    
                                }else if(msgdata.appname == 'peripheral'){
                                    let count = this.peripheralMsgCount;
                                    if(count>0){
                                        count--;
                                    }else{
                                        count++;
                                    }
                                    _g.peripheralMsgCount(count);
                                }
                                if(this.msgData.length> this.msgLimit|| this.msgData.length== this.msgLimit){
                                    this.msgData.pop();
                                }
                                this.msgData.unshift(msgdata);
                                this.msgCount++;
                                setLocal("msgData", this.msgData);
                                setLocal("msgCount", this.msgCount);
                            }
                        
                        }, false);
                    }
                    
                });
                   
            },

            collapseAsideNavbar(){
                this.$emit("collapseAsideNavbar", "")
            },

            confirmResetPwd(){
                this.$refs.ResetPasswordForm.validate((valid) => {
                    if (valid) {
                        let username = getSession("username")
                        if(this.ResetPasswordForm.oldPwd === this.ResetPasswordForm.newPwd){
                            this.$swal("",this.$t('header.samePassword'),"info", {button: this.$t('global.confirm')});
                            return;
                        }
                        if(this.ResetPasswordForm.newPwd === this.ResetPasswordForm.confirmNewPwd){
                            resetPasswd(username, this.ResetPasswordForm.oldPwd, this.ResetPasswordForm.newPwd).then((data) => {
                                handleResponse(data, (res) => {
                                    if(res.status === "CHANGED"){
                                        this.$swal('', this.$t('global.success'), "success", {button: this.$t('global.confirm')}).then(() => {
                                            this.$router.replace('/');
                                        })
                                    }else{
                                        _g.handleError(res);
                                    }
                                    this.dialogResetPasswordVisible = false;
                                })
                            })
                        }else{
                            this.$swal("",this.$t('header.passwordNotMatch'),"info", {button: this.$t('global.confirm')})
                        }
                    }
                })
            },

            getQRcodeData(){
               getQRcodeDataApi().then((data) => {
                    handleResponse((data), (res) => {
                        if(_g.isObject(res)){
                            this.qrCodeValue = JSON.stringify(res.data);
			    this.configValue = res.data.data;
                            this.qrIsShow = true;
                        }
                    })
                })
            },

            getRole(){
                let uname = getSession("username");
                if(uname){
                    getUserByNameApi(uname).then((res) => {
                        handleResponse(res, (data) => {
                            if(_g.isObject(data)){
                                this.role = data.role+"";
                                setSession("androidDMRole", this.role);
                            }
                        })
                    })
                }
            },
            showResetPwd(){
                this.dialogResetPasswordVisible = true;
                this.ResetPasswordForm = {
                    oldPwd: "",
                    newPwd: "",
                    confirmNewPwd: ""
                };
            },

            managerUser(){
                this.$router.replace("/main/user/list");
            },

            downloadConfigFile(){
                window.location.href="/sso/downloadConfigFile";
            },
            goAstore(){
                let newTab=window.open('about:blank');
                getRepoServerApi().then((aStoreServerAddress) => {
                    newTab.location.href = aStoreServerAddress+"#/main";
                });
            },
            
            copyConfigFile(){
                copy(this.configValue);
                this.$message({
                    message: this.$t('configuration.copyMessage'),
                    type: "success"
                });
            },

            copyServerId(){
               copy(this.serverId);
                this.$message({
                    message: this.$t('configuration.copyMessage'),
                    type: "success"
                }); 
            },

            getServerId(){
                getServerIdApi().then((data) => {
                    handleResponse((data), (res) => {
                        if(_g.isObject(res)){
                            this.serverId = res.serverId;
                            this.serverIdIsShow = true;
                        }
                    })
                })
            }, 
        },

        created(){
            this.showMsg();
            this.getuserinfo();   
            this.getQRcodeData();
            this.getRole();
            this.getServerId(); 
        },

        watch:{
            msgData:{
                handler(newval, oldval){
                    this.getuserinfo();
                },
                deep: true
            }
        },

        computed: {
            msgIsShow: {
                get(){
                    return this.msgCount> 0? true: false;
                },
                set(val){
                    this.msgIsShow = val;
                }
                
            },
            ...mapState({
                onlineDeviceCount: "onlineDeviceCount",
                deviceMsgCount: "deviceMsgCount",
                peripheralMsgCount: "peripheralMsgCount",
                switchLangCount: "switchLangCount",
                deviceStatusCount: "deviceStatusCount"
            }),  

        },

        mounted(){
            this.doEventSource();
        }
    }
</script>




